<template>
  <div class="flex">
    <aside>
        <Sidebar :list="list" base="singlewell" title="单井统计"/>
    </aside>
    <section>
        <router-view :route="route"/>
    </section>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Sidebar from '@/components/common/Sidebar';
export default {
  data () {
    return {
        list: [
            {
                txt: '钻头',
                url: 'borebit'
            },
            {
                txt: '螺杆',
                url: 'screw'
            },
            {
                txt: '钻井时效',
                url: 'drillingtime'
            },
            {
                txt: '复杂时效比率',
                url: 'complex'
            },
            {
                txt: '钻井液全性能',
                url: 'performance'
            }
        ]
    }
  },

  components: {
      Sidebar
  },

  computed: {
      ...mapGetters({
      route: 'route'
    }),
  },

  mounted: function() {},

  methods: {}
}
</script>

<style lang='scss' scoped>
.flex {
    height: 100%;
}
aside {
    width: 260px;
}
section {
    width: calc(100% - 260px);
}
</style>



